<template>
  <div class="DFRDBox">
    <div class="title">
        编辑项目角色
    </div>
    <el-divider></el-divider> 
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="角色名称">
            <el-input v-model="formInline.user" placeholder="长度2-8个字"></el-input>
        </el-form-item>
        <el-form-item label="备注">
            <el-input v-model="formInline.user" placeholder="可填"></el-input>
        </el-form-item>
    </el-form>
    <el-divider></el-divider> 
    <h5>权限列表</h5>
    <template>
        <el-table
            :data="tableData"
            stripe
            border
            style="width: 100%">
            <el-table-column
            prop="date"
            label="GLS地图"
            width="230">
            </el-table-column>
            <el-table-column
            prop="name"
            label="查看Gis">
            </el-table-column>
            <el-table-column
            prop="address"
            label="GIS地图">
            </el-table-column>
        </el-table>
    </template>
    <el-divider></el-divider> 
    <el-button type="primary" icon="el-icon-check">保存</el-button>
    <el-button>取消</el-button>
  </div>
</template>

<script>
export default {
    data(){
        return{
            formInline: {//角色名称/备注
                user: '',
                region: ''
            },
            tableData:[]//表格
        }
    },
    methods:{
    }
}
</script>

<style lang="stylus" scoped>
.DFRDBox
    padding 30px
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
    
</style>


